import {Editor} from '../Editor';
import {StoreController} from '../store/controllers/StoreController';

export class ExportsHelper {
	static url() {
		const scene = StoreController.engine.scene;
		const projectName = Editor.instance().api.projectName();
		return `https://polygonjs.com/exports/${projectName}/${scene.name()}`;
	}
	static localRelativePath() {
		const scene = StoreController.engine.scene;
		return `./public/polygonjs/build/${scene.name()}`;
	}
}
